@import 'fonts';
@import 'variables';
@import 'blocks/header/header';
@import 'blocks/header/media';

*, ::after, ::before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

body {
    padding: 0;
    margin: 0;
    background: #DBE5E7;
}

header, main, footer {
    margin: auto;
    max-width: 1920px;
}

a {
    text-decoration: none;
    cursor: pointer;
}

button {
    cursor: pointer;
}

ul {
    list-style: none;
}

.main-container {
    position: relative;
    background: $white-color;
    margin: auto;
    max-width: 1920px;
}

.container {
    width: 100%;
    margin: auto;
    padding: 0 $size-70px;
}

.button {
    padding: 0 14px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    height: 34px;
    font-family: "Golos Text", sans-serif;
    font-weight: 500;
    font-style: normal;
    border-radius: 17px;

    &_middle {
        height: 60px;
    }

    &__big {
        height: 80px;
    }
}

.border-gray {
    border: 1px solid $grey-border-color;
    color: $black-color;
}

.dark-bg {
    background: $dark-background;
    color: $white-color;
}

.light-blue-bg {
    background: $light-blue-background;
    color: $white-color;
    text-transform: uppercase;
}



//whatsapp-link
.whatsapp {
    margin-right: 10px;
    font-size: 15px;

    &::before {
        padding-right: 5px;
        color: #25D366;
    }
}

//main title block
.title-block {
    margin: auto;
    padding-top: 50px;
    padding-bottom: 20px;
    max-width: 1020px;
    text-align: center;
    h1 {
        margin-bottom: 30px;
        font-family: 'Golos Text', sans-serif;
        font-weight: 500;
        font-size: clamp(20px, 3.47vw, 80px);
        line-height: 1.05;
    }
    
    p {
        margin-bottom: 30px;
    }
}

.text {
    font-family: 'Golos Text', sans-serif;
    font-weight: 400;
    font-size: clamp(10px, 1.24vw, 30px);
    line-height: 1.2;
}

//feedback block
.feedback-block {
    margin-bottom: 50px;
   text-align: center;
    &__services {
        margin-left: 20px;
        color: $black-color;
        font-family: 'Golos Text', sans-serif;
        font-weight: 500;
        font-size: 14px;
        line-height: 1.2;
        text-transform: uppercase;
    }
}

.banner {
    margin-bottom: 70px;
    width: 100%;

    &__img {
        object-fit: contain;
        width: 100%;
        height: 100%;
    }

}

.key-word {
    display: inline-block;
    padding: 6px 14px;
    font-family: 'Golos Text', sans-serif;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.2;
    text-transform: uppercase;
    border-radius: 17px;
    border: 1px solid $grey-border-color;
}

.second-title {
    font-family: 'Golos Text', sans-serif;
    font-weight: 500;
    font-size: clamp(20px, 2.22vw, 70px);
    line-height: 1.1;
    color: $black-color;
}

.section {
    margin-bottom: $size-70px;
}

.our-business {
    text-align: center;

    &__key-word {
        margin-bottom: 30px;
    }

    .second-title {
        max-width: 970px;
        margin: auto;
    }
}

.feedback {
   &__wrapper {
    padding: clamp(20px, 3.46vw, 60px);
    display: flex;
    background: #EFF4F5;
    border-radius: 30px;
    justify-content: space-between;
    align-items: center;
    
    .second-title {
        width: clamp(200px, 34vw, 800px);

        &_string {
            display: block;
        }
    }
   }
}

.form {
    width: clamp(200px, 40.96vw, 800px);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 10px;
    font-size: 18px;

    &__input {
        padding-left: 20px;
        box-sizing: border-box;
        flex-grow: 1;
        height: clamp(40px, 4.16vw, 80px);
        border-radius: 20px;
        border: none;
        background: $white-color;
        
    }

    &__button {
        width: 100%;
        border-radius: 20px;
        height: clamp(40px, 4.16vw, 80px);
        font-weight: 500;
    }
   
}