.header {

    &__feedback {
        text-transform: uppercase;
    }
    &__box {
        // display: flex;
        // align-items: center;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        align-items: center;
    }

    .logo {
        display: block;
        width: 190px;
        height: 60px;

        &__img {
            object-fit: contain;
            width: 100%;
            height: 100%;
        }
    }

    .menu {
        flex-grow: 1;
        
        &__list {
            display: flex;
            justify-content: center;
            gap: 20px;
        }

        &__link {
            display: block;
            position: relative;
            font-size: 15px;
            color: $black-color;
            bottom: 0px;
            transition: bottom .2s ease-in-out;

            &:hover {
                bottom: 2px;
            }
        }

    }   

    &__contact {
        justify-self: end;

    }

  
}